.game-name{
    font: 20px Verdana;
    text-align: center;
}

.square {
    width: 100px;
    height: 100px;
    background: #fff;
    border: 1px solid rgb(182, 66, 66);
    font: 50px Arial;
    text-align: center;
    float: left;
}

.square:focus {
    outline: none;
}

.game-board {
    display: flex;
}

.game {
    position: absolute;
}